	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sprint Backlog</h2>\n<p>The Sprint Backlog is a list of tasks identified by the Scrum Team to be completed during the Scrum Sprint. During the Sprint Planning meeting, the team selects a number of Product Backlog items, usually in the form of user stories, and identifies the tasks necessary to complete each user story. </p>\n<p>It is critical that the team itself selects the items and size of the Sprint Backlog. Because they are the people implementing / completing the tasks, they must be the people to choose what they are forecating to achive during the Sprint.</p>\n<h4>More Information:</h4>\n<p><a href=\"http://www.scrumguides.org/scrum-guide.html#artifacts-sprintbacklog\">Scrum Guide: Sprint Backlog</a></p>","fields":{"slug":"/agile/sprint-backlog/"},"frontmatter":{"title":"Sprint Backlog"}}},"pathContext":{"slug":"/agile/sprint-backlog/"}}

